<template>
  <div class="tw-rounded bg-white mb-2">
    <div class="flex justify-center">
      <div class="w-full py-2 md:px-5">
        <div class="flex justify-between items-center px-3 md:p-0">
          <button class="w-6 h-6 mr-2" @click="$router.back()">
            <img src="/images/hero-back.svg" alt="back" />
          </button>
          <div v-if="!isEditingName" class="mx-2 text-2xl">{{ name }}</div>
          <button
            v-if="!isEditingName && name.length > 0"
            class="px-2 mx-2"
            @click="onEditName()"
          >
            <img class="w-6 h-6" src="/images/hero-edit.svg" />
          </button>
          <input
            class="rounded-lg border-2 border-black px-2"
            v-if="isEditingName"
            v-model="inputName"
          />
          <div v-if="isEditingName">
            <button @click="onEditNameConfirm()" class="ml-2">
              <img class="w-8 h-8" src="/images/hero-confirm-outline.svg" />
            </button>
            <button @click="onEditNameCancel()" class="ml-2">
              <img class="w-8 h-8" src="/images/hero-x-outline.svg" />
            </button>
          </div>
        </div>
      </div>
    </div>

    <div v-if="errorMessage.length > 0" class="text-red-600 pb-2">
      {{ errorMessage }}
    </div>

    <div class="pb-0 md:pb-5 px-0 md:px-5">
      <div class="flex justify-center item-start">
        <iframe
          class="tw-iframe"
          ref="myiframe"
          src="/kbEditWall/main.html"
          :width="frameWidth"
          :height="frameHeight"
          scrolling="no"
          frameborder="0"
        >
        </iframe>
      </div>
    </div>
  </div>
  <!-- search -->
  <div class="tw-rounded bg-white p-2">
    <div class="mb-2 flex justify-center items-center">
      <input class="w-4 h-4 mr-2" type="checkbox" v-model="isShowMine" />
      <div>내꺼만 표시</div>
    </div>
    <div v-if="!isShowMine" class="mb-2 flex justify-center">
      <input
        class="mr-2 px-2 rounded-lg border-2 border-black"
        v-model="inputSearch"
      />
      <button
        v-if="!loadingSearch"
        class="px-4 py-1 rounded-lg bg-indigo-600 text-white"
        @click="onSearch()"
      >
        검색
      </button>
    </div>
    <div class="grid grid-cols-3 gap-1 px-2">
      <div
        class="w-24"
        v-for="data in listPic"
        :key="data.rid"
        @click="onSearchSelect(data)"
      >
        <img class="h-24" :src="$store.makePicUrl(data.pic_url)" />
        <div class="font-bold">{{ data.pic_name }}</div>
      </div>
    </div>

    <button
      v-if="isSearchMore"
      class="px-4 py-1 rounded-lg bg-indigo-600 text-white"
      @click="onSearchMore()"
    >
      더보기
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      iframeWin: null,

      wallRid: 0,
      name: "",
      loading: false,
      loadingSuccess: false,
      errorMessage: "",
      frameWidth: "100%",
      frameHeight: "100%",

      isEditingName: false,
      inputName: "",
      loadingEditName: false,

      isShowMine: true,
      inputSearch: "",
      loadingSearch: false,
      errorMessageSearch: "",
      isSearchMore: false,
      searchDatas: [],
    };
  },
  async mounted() {
    // console.log("WallEdit mounted");
    this.iframeWin = this.$refs.myiframe.contentWindow;
    this.wallRid = parseInt(this.$route.params.rid, 10);
    this.searchDatas = [
      // { rid: 30, pic_name: "30", pic_url: "http://localhost/update/30.png" },
      // { rid: 31, pic_name: "31", pic_url: "http://localhost/update/31.png" },
      // { rid: 32, pic_name: "32", pic_url: "http://localhost/update/32.png" },
    ];

    window.addEventListener("message", this.procMessage);
  },
  unmounted() {
    window.removeEventListener("message", this.procMessage);
  },
  computed: {
    listPic() {
      if (this.isShowMine) {
        // console.log("listPic isShowMine", typeof this.$store.state.listPicture);
        if (Array.isArray(this.$store.state.listPicture)) {
          // console.log("listPic listPicture", this.$store.state.listPicture);
          // url 값이 비어있는 경우는 제외해야한다.
          const arr = [];
          this.$store.state.listPicture.forEach((data) => {
            if (data.pic_url == "") return;
            arr.push(data);
          });

          return arr;
        }
        return [];
      } else {
        return this.searchDatas;
      }
    },
  },
  methods: {
    async procMessage(event) {
      if (event.data.cmd === undefined) return;
      if (event.data.cmd.indexOf("kbEditWall") < 0) return;

      const recv = event.data;
      console.log("WallEdit.vue procMessage", recv);

      if (recv.cmd == "kbEditWall.loadingComplete") {
        // await this.$store.wait(1000);

        const reqData = {
          api: "getWall",
          loginToken: localStorage.getItem("loginToken"),
          userUid: this.$store.getters.userUid,
          wallRid: this.wallRid,
          needPic: this.$store.state.listPicture === null,
          isVisit: false,
        };
        // console.log("getWall req", reqData);

        const res = await this.$store.callAsync(reqData);
        if (res.code != 100) {
          this.errorMessage = res.message;
          return;
        }

        console.log("getWall res ok", res);
        this.name = res.wall.wall_name;
        // if (res.listPicture !== undefined) {
        //   this.$store.commit("setListPicture", res.listPicture);
        // }

        this.$store.postMessage(this.iframeWin, {
          cmd: "kbEditWall.firstData",
          wall_data: res.wall.wall_data,
          visit_list: res.visit_list,
        });
      } else if (recv.cmd == "kbEditWall.frameSize") {
        // console.log("frameSize", recv.w, recv.h);
        this.frameWidth = recv.w;
        this.frameHeight = recv.h;
      } else if (recv.cmd == "kbEditWall.save") {
        console.log("kbEditWall.save", recv);

        await this.$store.wait(1000); // 연타방지

        const reqWeb = {
          api: "saveWall",
          loginToken: localStorage.getItem("loginToken"),
          userUid: this.$store.getters.userUid,
          wallRid: this.wallRid,
          width: recv.width,
          height: recv.height,
          arrid: recv.arrid,
          arrimg: recv.arrimg,
          bg_id: recv.bg_id,
        };

        this.errorMessage = "";
        const res = await this.$store.callAsync(reqWeb);
        const sendData = { cmd: "kbEditWall.saveComplete" };
        if (res.code == -3) {
          // 변동사항없음 저장하지 않음
          console.log("saveWall skip");
        } else if (res.code != 100) {
          console.log("saveWall ng", res);
          this.errorMessage = res.message;
        } else {
          console.log("saveWall ok");
          this.$store.commit("updateWall", res.wall);
        }
        this.$store.postMessage(this.iframeWin, sendData);
      } else {
        console.error("WallEdit unknown cmd", recv.cmd);
      }
    },
    async onEditName() {
      this.isEditingName = true;
      this.inputName = this.name;
    },
    async onEditNameConfirm() {
      const reqData = {
        api: "updateWallName",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        wallRid: this.wallRid,
        newWallName: this.inputName,
      };
      console.log("onEditNameConfirm", reqData);

      this.errorMessage = "";
      this.loadingEditName = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingEditName = false;

      if (res.code != 100) {
        this.errorEditName = res.message;
        return;
      }

      console.log("onEditNameConfirm res", res);

      this.$store.commit("updateWall", {
        rid: this.wallRid,
        wall_name: res.newWallName,
      });
      this.name = res.newWallName;
      this.isEditingName = false;
    },
    onEditPicNameCancel() {
      this.isEditingName = false;
    },
    async onSearch() {
      const reqData = {
        api: "searchImage",
        loginToken: localStorage.getItem("loginToken"),
        userUid: this.$store.getters.userUid,
        inputSearch: this.inputSearch,
      };
      console.log("onSearch req", reqData);

      this.errorMessageSearch = "";
      this.isSearchMore = false;
      this.loadingSearch = true;
      const res = await this.$store.callAsync(reqData);
      this.loadingSearch = false;

      if (res.code != 100) {
        this.errorMessageSearch = res.message;
        return;
      }

      console.log("onSearch res", res);

      this.searchDatas = res.searchDatas;
      this.isSearchMore = res.isSearchMore;
    },
    onSearchMore() {},
    onSearchSelect(data) {
      console.log("onClickSearch", data);
      this.$store.postMessage(this.iframeWin, {
        cmd: "kbEditWall.addImage",
        id: data.rid,
        url: data.pic_url,
      });
    },
  },
};
</script>
